/*********--CommonParts--***********/
body {
  background-color: #1b2fce;
  word-break: break-all;
  overflow-x: hidden;
  font-weight: 200;
  letter-spacing: 0.2rem;
  color: #1b2fce;
  line-height: 1; }

h1,
h1 .sp-line-break,
.title-span {
  font-size: 6rem; }

h2 {
  font-size: 5rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.5rem; }

h1, h2, h3, h4, dt, .title-span, h1 .sp-line-break {
  font-weight: 900;
  text-shadow: 2px 2px rgba(19, 25, 99, 0.419608); }

@media screen and (max-width: 1024px) {
  h2 {
    font-size: 3.5rem !important; } }

@media screen and (max-width: 468px) {
  h1,
  h1 .sp-line-break {
    font-size: 4rem; }
  h2 {
    font-size: 2.5rem !important; } }

p, li {
  font-size: 1.8rem; }

p {
  line-height: 1.6; }

.container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px; }

.btn {
  max-width: 180px;
  width: 100%;
  padding: 10px 0;
  display: block;
  text-align: center;
  color: #15DB95;
  font-size: 1.8rem;
  border: 1px solid #15DB95;
  -webkit-transition: .5s;
  transition: .5s; }

.btn:hover {
  background: #15DB95;
  color: white;
  border: 2px solid #15DB95; }

.square-btn {
  position: relative; }
  .square-btn:hover:before {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg); }

.square-btn:before {
  content: "";
  width: 10px;
  height: 10px;
  margin-left: 10px;
  background: #15DB95;
  display: inline-block;
  position: absolute;
  right: -15px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transform: rotateZ(0deg);
          transform: rotateZ(0deg); }

.line-break {
  display: block; }

.pop-up {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: 1.5s;
  transition: 1.5s;
  background: rgba(27, 47, 206, 0.61);
  opacity: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.pop-up:hover {
  opacity: 1; }

@media screen and (max-width: 768px) {
  .sp-line-break {
    display: block; } }

/* Navigation */
.button_container {
  width: 35px;
  height: 27px;
  margin: auto;
  position: fixed;
  top: 0;
  right: 2%;
  bottom: 0;
  cursor: pointer;
  z-index: 100;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease; }

.button_container:hover {
  opacity: 0.7; }

.button_container.active .top {
  -webkit-transform: translateY(11px) translateX(0) rotate(45deg);
  transform: translateY(11px) translateX(0) rotate(45deg);
  background: white; }

.button_container.active .middle {
  opacity: 0;
  background: white; }

.button_container.active .bottom {
  -webkit-transform: translateY(-11px) translateX(0) rotate(-45deg);
  transform: translateY(-11px) translateX(0) rotate(-45deg);
  background: white; }

.button_container span {
  background: white;
  border: none;
  height: 5px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
  cursor: pointer; }

.button_container span:nth-of-type(2) {
  top: 11px; }

.button_container span:nth-of-type(3) {
  top: 22px; }

.overlay {
  position: fixed;
  background: #151c65;
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: opacity 0.35s, visibility 0.35s, height 0.35s;
  transition: opacity 0.35s, visibility 0.35s, height 0.35s;
  overflow: hidden;
  z-index: 99999; }
  .overlay nav {
    position: relative;
    height: 70%;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    font-weight: 400;
    text-align: center; }
  .overlay nav > ul {
    list-style: none;
    padding: 0;
    margin: 0 auto;
    display: inline-block;
    position: relative;
    height: 100%; }
  .overlay nav > ul > li {
    display: block;
    height: calc(100% / 3);
    min-height: 50px;
    position: relative;
    opacity: 0; }
  .overlay nav > ul > li > a {
    display: block;
    position: relative;
    color: white;
    text-decoration: none;
    overflow: hidden;
    font-size: 5.0rem; }
  .overlay nav > ul > li > a:hover:after,
  .overlay nav > ul > li > a:focus:after,
  .overlay nav > ul > li > a:active:after {
    width: 100%; }
  .overlay nav > ul > li > a:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    height: 3px;
    background: white;
    -webkit-transition: 0.35s;
    transition: 0.35s; }
  .overlay nav ul.sns {
    justify-content: space-between; }

.overlay.open {
  opacity: 0.9;
  visibility: visible;
  height: 100%;
  z-index: 2; }
  .overlay.open li {
    -webkit-animation: fadeInRight 0.5s ease forwards;
    animation: fadeInRight 0.5s ease forwards;
    -webkit-animation-delay: 0.35s;
    animation-delay: 0.35s; }
  .overlay.open li:nth-of-type(2) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s; }
  .overlay.open li:nth-of-type(3) {
    -webkit-animation-delay: 0.45s;
    animation-delay: 0.45s; }
  .overlay.open li:nth-of-type(4) {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s; }

@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

@keyframes fadeInRight {
  0% {
    opacity: 0;
    left: 20%; }
  100% {
    opacity: 1;
    left: 0; } }

/* SNS */
.sns {
  position: fixed;
  top: 0;
  right: 2%;
  z-index: 100; }

.fa {
  width: 40px;
  padding: 10px 0px;
  font-size: 2.0rem;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 100%; }

.fa-facebook {
  color: white; }

.fa-linkedin {
  color: white; }

.fa:hover {
  opacity: 0.7; }

.load-overlay {
  width: 100%;
  height: 100%;
  margin-top: 0 !important;
  position: absolute;
  top: 0;
  left: 0;
  display: flex; }

.delighter.started.load-overlay {
  visibility: hidden; }

@media screen and (max-width: 1024px) {
  .load-overlay,
  .load-overlay li {
    display: none; } }

